<template>
    <div class="upload-form">
        <el-upload
            class="upload-card"
            :class="{'is-uploading': uploading, 'upload-card-busy': fileList.length}"
            drag
            :http-request="uploadFile"
            multiple
            :onSuccess="handleSuccess"
            :on-exceed="handleExceed"
            :on-error="handleError"
            :before-upload="beforeUpload"
            :on-progress="handleProgress"
            :file-list="fileList"
            :show-file-list="false"
            :limit="10"
            accept="image/*, video/*">
            <el-icon class="el-icon--upload">
                <CameraFilled color="blanchedalmond"/>
            </el-icon>
            <div class="el-upload__text">  <em></em></div>
            <template #tip>
                <div class="el-upload__tip">105MB</div>
            </template>
        </el-upload>
        <Transition name="upload-list-trans">
            <el-card class="upload-list-card" v-if="fileList.length">
                <div class="upload-list-container">
                    <el-scrollbar>
                        <div class="upload-list-dashboard">
                            <el-text class="upload-list-dashboard-title">
                                <el-icon><List /></el-icon>{{ uploadingCount }}
                                <el-icon><Checked /></el-icon>{{ uploadSuccessCount }}
                                <el-icon><Failed /></el-icon>{{ uploadErrorCount }}
                            </el-text>
                            <div class="upload-list-dashboard-action">
                                <el-button-group>
                                    <el-tooltip content="" placement="top">
                                        <el-button type="primary" round @click="copyAll" alt=""><el-icon><Grid /></el-icon></el-button>
                                    </el-tooltip>
                                    <el-tooltip content="" placement="top">
                                        <el-button type="primary" round @click="fileList = []"><el-icon><CircleClose /></el-icon></el-button>
                                    </el-tooltip>
                                </el-button-group>
                            </div>
                        </div>
                        <div class="upload-list-item" v-for="file in fileList" :key="file.name" :span="8">
                            <img
                                style="width: 10vw; border-radius: 12px;"
                                :src="file.url"
                                @error="file.url = 'https://imgbed.sanyue.site/file/b6a4a65b4edba4377492e.png'"
                                >
                            </img>
                            <div class="upload-list-item-content">
                                <el-text class="upload-list-item-name" truncated>{{ file.name }}</el-text>
                                <div class="upload-list-item-url" v-if="file.status==='done'">
                                    <el-link :underline="false" :href="file.url" target="_blank">
                                        <el-text class="upload-list-item-url-text" truncated>{{ file.url }}</el-text>
                                    </el-link>
                                </div>
                                <div class="upload-list-item-progress" v-else>
                                    <el-progress :percentage="file.progreess" :status="file.status" :show-text="false"/>
                                </div>
                            </div>
                            <div class="upload-list-item-action">
                                    <el-button type="primary" circle size="medium" class="upload-list-item-action-button" @click="handleCopy(file)">
                                        <el-icon><Link /></el-icon>
                                    </el-button>
                                    <el-button type="danger" circle size="medium" class="upload-list-item-action-button" @click="handleRemove(file)">
                                        <el-icon><Delete /></el-icon>
                                    </el-button>
                            </div>
                        </div>
                    </el-scrollbar>
                </div>
            </el-card>
        </Transition>
    </div>
</template>

<script>
import axios from 'axios'
export default {
name: 'UploadForm',
data() {
    return {
        fileList: [],
        uploading: false
    }
},
computed: {
    uploadSuccessCount() {
        return this.fileList.filter(item => item.status === 'done' || item.status === 'success').length
    },
    uploadErrorCount() {
        return this.fileList.filter(item => item.status === 'exception').length
    },
    uploadingCount() {
        return this.fileList.filter(item => item.status === 'uploading').length
    }
},
methods: {
    uploadFile(file) {
        const formData = new FormData()
        formData.append('file', file.file)
        axios({
            url: '/upload',
            method: 'post',
            data: formData,
            onUploadProgress: (progressEvent) => {
                const percentCompleted = Math.round((progressEvent.loaded / progressEvent.total) * 100)
                file.onProgress({ percent: percentCompleted, file: file.file })
            }
        }).then(res => {
            file.onSuccess(res)
        }).catch(err => {
            file.onError(err)
        }).finally(() => {
            this.uploading = false
        })
    },
    handlePreview(file) {
        window.open(file.url, '_blank')
    },
    handleRemove(file) {
        this.fileList = this.fileList.filter(item => item.uid !== file.uid)
        this.$message({
            type: 'info',
            message: file.name + ''
        })
    },
    handleSuccess(response, file) {
        try {       
            this.fileList.find(item => item.uid === file.uid).url = process.env.VUE_APP_SITE_URL + response.data[0].src
            this.fileList.find(item => item.uid === file.uid).progreess = 100
            this.fileList.find(item => item.uid === file.uid).status = 'success'
            this.$message({
                type: 'success',
                message: file.name + ''
            })
            setTimeout(() => {
                this.fileList.find(item => item.uid === file.uid).status = 'done'
            }, 3000)
        } catch (error) {
            this.$message.error(file.name + '')
            this.fileList.find(item => item.uid === file.uid).status = 'exception'
        } finally {
            this.uploading = false
        }
    },
    handleExceed(files) {
        this.$message.warning(` 10 `)
    },
    handleError(err, file) {
        this.$message.error(file.name + '')
        this.uploading = false
        this.fileList.find(item => item.uid === file.uid).status = 'exception'
    },
    handleCopy(file) {
        const status = this.fileList.find(item => item.uid === file.uid).status
        if (status !== 'done' && status !== 'success') {
            this.$message({
                type: 'warning',
                message: ''
            })
            return
        }
        navigator.clipboard.writeText(file.url)
        this.$message({
            type: 'success',
            message: ''
        })
    },
    beforeUpload(file) {
        const isLt5M = file.size / 1024 / 1024 < 5
        if (!isLt5M) {
            this.$message.error(' 5MB!')
        } else {
            this.uploading = true
            const fileUrl = URL.createObjectURL(file)
            this.fileList.push({
                uid: file.uid,
                name: file.name,
                url: fileUrl,
                status: 'uploading',
                progreess: 0
            })
        }
        return isLt5M
    },
    handleProgress(event) {
        this.fileList.find(item => item.uid === event.file.uid).progreess = event.percent
    },
    copyAll() {
        const urls = this.fileList.map(item => {
            if (item.status === 'done' || item.status === 'success') {
                return item.url
            }
        }).join('\n')
        navigator.clipboard.writeText(urls)
        this.$message({
            type: 'success',
            message: ''
        })
    }
}
}
</script>

<style scoped> 
@keyframes breathe {
    0%, 100% {
    }
    50% {
        box-shadow: 0 0 10px 5px #409EFF;
        opacity: 0.8;
    }
}
.upload-form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.upload-list-trans-enter-active .upload-list-trans-leave-active {
    transition: all 3s ease;
}
.upload-list-trans-enter, .upload-list-trans-leave-to {
    opacity: 0;
    transform: translateY(20vh);
}
.upload-list-card {
    width: 55vw;
    height: 35vh;
    margin-top: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-radius: 15px;
    opacity: 0.8;
    background-color: rgba(255, 255, 255, 0.7);
    backdrop-filter: blur(10px);
}
.upload-list-container {
    width: 55vw;
    height: 35vh;
}
.upload-list-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 5px;
    border: 1px solid #a5bef7;
    padding: 5px;
    border-radius: 15px;
}
.upload-list-item-name {
    font-size: small;
    font-weight: bold;
    width: 28vw;
}
.upload-list-item-content {
    display: flex;
    flex-direction: column;
    margin-left: 10px;
}
.upload-list-item-url-text {
    width: 28vw;
}
.upload-list-item-progress {
    margin-top: 3px;
    width: 28vw;
}
.upload-list-item-action {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.upload-list-item-action-button {
    margin: 2px;
}
.upload-card {
    width: 55vw;
    padding: 20px;
    background: none;
}
.upload-card-busy :deep(.el-upload-dragger) {
    height: 25vh;
}
:deep(.el-upload-dragger)  {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 50vh;
    border-radius: 15px;
    border: 3px dashed #409EFF;
    opacity: 0.7;
    background-color: rgba(255, 255, 255, 0.6);
    backdrop-filter: blur(10px);
    transition: all 0.3s ease;
}
:deep(.el-upload-dragger:hover) {
    opacity: 0.8;
    box-shadow: 0 0 10px 5px #409EFF;
}
:deep(.el-upload-dragger.is-dragover) {
    opacity: 0.8;
    box-shadow: 0 0 10px 5px #409EFF;
}
.is-uploading :deep(.el-upload-dragger){
    animation: breathe 3s infinite;
}
.el-upload__text {
    font-weight: bold;
    font-size: medium;
    user-select: none;
}
.el-upload__tip {
    font-size: small;
    color: antiquewhite;
    user-select: none;
}
.upload-list-dashboard {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
}
.upload-list-dashboard-title {
    font-size: medium;
    font-weight: bold;
}
</style>